@font-face {
  font-family: "Avenir-Black";
  src: url("../font/Avenir-Black.woff2") format("woff2"), url("../font/Avenir-Black.woff") format("woff"), url("../font/Avenir-Black.ttf") format("truetype");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Avenir-Heavy";
  src: url("../font/Avenir-Heavy.woff2") format("woff2"), url("../font/Avenir-Heavy.woff") format("woff"), url("../font/Avenir-Heavy.ttf") format("truetype");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Avenir-Medium";
  src: url("../font/Avenir-Medium.woff2") format("woff2"), url("../font/Avenir-Medium.woff") format("woff"), url("../font/Avenir-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Avenir-Regular";
  src: url("../font/Avenir-Regular.woff2") format("woff2"), url("../font/Avenir-Regular.woff") format("woff"), url("../font/Avenir-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
:root {
  --pc: #081E3B;
  --sc: #374455;
  --anchor: #657283;
  --anchor-hover: #F07137;
  --gray-bg: #F4F4F4;
  --footer-bg: #818181;
  --white: #FFFFFF;
  --tf: transparent;
  --p-color: #0F0F0F;
  --h1toh6: #0F0F0F;
  --line-light: #D9D9D9;
  --orange: #F07137;
}

:root {
  --ani-s: all 0.2s 0s ease;
  --ani-t: all 0.3s 0s ease;
  --ani-f: all 0.5s 0s ease;
  --op-thr: 0.3;
  --op-n: 0.9;
  --op-e: 0.8;
  --op-f: 0.5;
}

.icon-save {
  background-color: green;
}

.icon-cancel {
  background-color: gray;
}

.icon-delete {
  background-color: red;
}

body,
html {
  font-family: "Avenir-Regular";
  line-height: 1.5em;
  font-size: 16px;
}

* {
  font-family: "Avenir-Regular";
}

.h1,
h1,
.h2,
h2,
.h3,
h3,
.h4,
h4,
.h5,
h5,
.h6,
h6 {
  color: var(--h1toh6);
  margin: 0;
  padding: 0;
}

.h1,
h1 {
  font-weight: 700;
  line-height: 1.1em;
  font-size: 53px;
}

.h2,
h2 {
  font-family: "Roboto";
  font-weight: 700;
  line-height: 48px;
  font-size: 38px;
}

.h3,
h3 {
  font-family: "Avenir-Medium";
  line-height: 34px;
  font-size: 24px;
}

.h4,
h4 {
  font-family: "Avenir-Heavy";
  line-height: 22px;
  font-size: 16px;
}

.h5,
h5 {
  font-family: "Avenir-Regular";
  line-height: 22px;
  font-size: 16px;
  text-transform: none;
}

.h6,
h6 {
  font-weight: 700;
  line-height: 1.5;
  font-size: 16px;
}

p {
  font-family: "Avenir-Regular";
  font-size: 16px;
  line-height: 26px;
  color: var(--sc);
  margin: 0;
  padding: 0;
}

b {
  font-weight: 700;
}

a {
  color: var(--anchor);
}
a:hover {
  color: var(--anchor-hover);
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
}

header {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  background-color: var(--white);
  z-index: 9999;
}

.navbar-nav .nav-link {
  font-family: "Avenir-Medium";
  font-size: 14px;
  color: var(--anchor);
  line-height: 28px;
}
.navbar-nav .nav-link.active, .navbar-nav .nav-link:hover {
  font-family: "Avenir-Black";
  color: var(--anchor-hover);
}
.navbar-nav .nav-item {
  display: flex;
  align-items: center;
}
.navbar-nav .nav-item .icons-nav-div {
  display: flex;
  align-items: center;
}
.navbar-nav .nav-item .icons-nav-div .nav-link {
  padding: 0 10px;
}
.navbar-nav .nav-item .icons-nav-div .border-link {
  border-left: 1px solid rgba(0, 0, 0, 0.1607843137);
  border-right: 1px solid rgba(0, 0, 0, 0.1607843137);
}

.navbar-expand-sm {
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1607843137);
}
.navbar-expand-sm .navbar-nav {
  gap: 15px;
}
.navbar-expand-sm .navbar-collapse {
  justify-content: flex-end;
}

footer {
  padding: 50px 0 0;
  background-color: #0D2234;
}
footer .footer-main-div {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  padding-bottom: 25px;
}
footer .footer-main-div .footer-logo-div {
  grid-area: 1/1/2/2;
}
footer .footer-main-div .footer-logo-div .footer-logo {
  width: 128px;
  height: 52px;
  padding: 5px 15px;
  background-color: var(--white);
  border-radius: 5px;
}
footer .footer-main-div .footer-logo-div p {
  font-family: "Avenir-Regular";
  font-size: 14px;
  color: #BABABA;
  line-height: 24px;
  margin-top: 10px;
  width: 85%;
}
footer .footer-main-div .quick-links {
  grid-area: 1/2/3/3;
}
footer .footer-main-div .site-map {
  grid-area: 1/3/3/4;
}
footer .footer-main-div .get-in-touch {
  grid-area: 1/4/3/5;
}
footer .footer-main-div .get-in-touch .get-in-touch-main {
  margin-top: 15px;
}
footer .footer-main-div .get-in-touch .get-in-touch-main div {
  display: flex;
  align-items: flex-start;
  gap: 10px 10px;
  grid-gap: 10px 10px;
}
footer .footer-main-div .get-in-touch .get-in-touch-main div img {
  margin-top: 5px;
}
footer .footer-main-div .get-in-touch .get-in-touch-main div a:hover, footer .footer-main-div .get-in-touch .get-in-touch-main div p:hover {
  font-family: "Avenir-Regular";
}
footer .footer-main-div .follow-us {
  grid-area: 2/1/3/2;
  margin-top: 50px;
}
footer .footer-main-div .follow-us .social-icon {
  display: flex;
  gap: 10px 10px;
  grid-gap: 10px 10px;
  margin-top: 10px;
}
footer .footer-main-div .follow-us .social-icon a:hover .box {
  fill: #F07137;
}
footer .footer-main-div .follow-us .social-icon a:hover .box-icon path {
  fill: var(--white);
}
footer .footer-main-div .footer-title {
  font-family: "Avenir-Black";
  font-size: 18px;
  color: var(--white);
  line-height: 30px;
  border-bottom: 1px solid rgba(232, 232, 232, 0.2);
  padding-bottom: 5px;
}
footer .footer-main-div .footer-links {
  font-family: "Avenir-Regular";
  font-size: 14px;
  color: #BABABA;
  line-height: 30px;
  text-decoration: none;
}
footer .footer-main-div .footer-links:hover {
  font-family: "Avenir-Heavy";
  color: var(--white);
}
footer .footer-main-div p {
  font-family: "Avenir-Regular";
  font-size: 14px;
  color: #BABABA;
  line-height: 30px;
  text-decoration: none;
}
footer .footer-main-div p:hover {
  color: var(--white);
}
footer .footer-main-div ul {
  margin-top: 15px;
}
footer .footer-copy {
  background-color: #091B2B;
}
footer .footer-copy .copyright-div {
  font-family: "Avenir-Regular";
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px 0;
}
footer .footer-copy .copyright-div p, footer .footer-copy .copyright-div a {
  font-size: 14px;
  color: #BABABA;
  line-height: 26px;
  text-decoration: none;
}

@media (min-width: 1400px) {
  .container,
.container-lg,
.container-md,
.container-sm,
.container-xl,
.container-xxl {
    max-width: 1112px;
  }
}
.pc {
  color: var(--pc);
}

.sc {
  color: var(--sc);
}

.banner-title {
  font-family: "Avenir-Black";
  font-size: 42px;
  color: var(--white);
  line-height: 52px;
}

.btn, .btn-primary {
  font-family: "Avenir-Heavy";
  padding: 13px 28px;
  border-radius: 6px;
  border: unset;
}
.btn:first-child:active, .btn-primary:first-child:active {
  color: var(--white);
  background-color: var(--orange);
}
.btn:not(.btn-check) + .btn:active, .btn-primary:not(.btn-check) + .btn:active, .btn:not(.btn-check) + .btn-primary:active, .btn-primary:not(.btn-check) + .btn-primary:active {
  color: var(--pc);
  background-color: var(--white);
}

.btn-primary-orange {
  color: var(--white);
  background-color: var(--orange);
}
.btn-primary-orange:hover, .btn-primary-orange:active, .btn-primary-orange:focus-visible {
  color: var(--white) !important;
  background-color: var(--orange) !important;
}
.btn-primary-white {
  color: var(--pc);
  background-color: var(--white);
}
.btn-primary-white:hover, .btn-primary-white:active, .btn-primary-white:focus-visible {
  color: var(--pc) !important;
  background-color: var(--white) !important;
}
.btn-primary-tran {
  color: var(--pc);
  border: 1px solid var(--pc);
  border-radius: 6px;
}
.btn-primary-tran:hover, .btn-primary-tran:active, .btn-primary-tran:focus-visible {
  border: 1px solid var(--pc);
  background-color: transparent !important;
}

.sec-title {
  font-size: 38px;
  font-weight: 400;
  color: var(--pc);
  line-height: 48px;
}
.sec-title b {
  font-family: "Roboto";
}

.overlay {
  overflow: auto;
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 99999;
  display: none;
}

.popup {
  max-width: 1000px;
  width: 90%;
  height: auto;
  padding: 0px;
  position: relative;
  background: #fff;
  margin: 50px auto 10px;
  border-radius: 0px;
  width: 50%;
  border-radius: 10px 10px 10px 10px;
}

.close {
  text-align: center;
  background: #fff;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: -10px;
  right: -10px;
  cursor: pointer;
  font-size: 26px;
  z-index: 99;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  box-shadow: 1px 5px 6px 2px rgba(0, 0, 0, 0.22);
}

.conatct-us-from {
  padding: 20px;
}
.conatct-us-from .sec-title {
  margin-bottom: 25px;
  font-weight: 600;
}

.form-control-div {
  position: relative;
  padding-bottom: 20px;
}
.form-control-div input, .form-control-div textarea {
  width: 100%;
  font-size: 14px;
  padding: 8px 15px;
}
.form-control-div .submit-btn {
  width: auto;
  padding: 8px 30px;
  border-radius: 6px;
  color: var(--white);
  background-color: var(--orange);
  border: 1px solid var(--orange);
}
.form-control-div .wpcf7-not-valid-tip {
  position: absolute;
  bottom: -35px;
  font-size: 10px;
  left: 0px;
}

@media only screen and (max-width: 1024px) {
  .popup {
    width: 95%;
  }
}
@media (max-width: 767px) {
  .banner-title {
    font-family: "Avenir-Black";
    font-size: 28px;
    color: var(--white);
    line-height: 38px;
    text-align: center;
  }
  .sec-title {
    font-size: 28px;
    font-weight: 400;
    color: var(--pc);
    line-height: 38px;
    text-align: center;
  }
  footer .footer-main-div {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, auto);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
  }
  footer .footer-main-div .footer-logo-div {
    grid-area: 1/1/2/3;
  }
  footer .footer-main-div .quick-links {
    grid-area: 2/1/3/2;
  }
  footer .footer-main-div .site-map {
    grid-area: 2/2/3/3;
  }
  footer .footer-main-div .get-in-touch {
    grid-area: 3/1/4/3;
  }
  footer .footer-main-div .follow-us {
    grid-area: 4/1/5/3;
  }
  footer .footer-main-div .quick-links, footer .footer-main-div .site-map {
    margin-top: 25px;
  }
  footer .footer-main-div .get-in-touch {
    margin-top: 30px;
  }
  footer .footer-main-div .follow-us {
    margin-top: 40px;
  }
  footer .footer-copy .copyright-div {
    flex-direction: column;
  }
}
#Path_99330 {
  animation: dash 50s linear alternate infinite;
}

.svg_sub {
  opacity: 0;
  animation: fade-in 1s ease 0s normal both;
}
.svg_sub_1 {
  animation-delay: calc(1s * 1);
}
.svg_sub_2 {
  animation-delay: calc(1s * 2);
}
.svg_sub_3 {
  animation-delay: calc(1s * 3);
}
.svg_sub_4 {
  animation-delay: calc(1s * 4);
}
.svg_sub_5 {
  animation-delay: calc(1s * 5);
}
.svg_sub_6 {
  animation-delay: calc(1s * 6);
}
.svg_sub_7 {
  animation-delay: calc(1s * 7);
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes dash {
  from {
    stroke-dashoffset: 2000;
  }
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes lineroad {
  to {
    stroke-dashoffset: 0;
  }
}
