.home-banner {
  height: 100vh;
  position: relative;
  display: flex;
  align-items: center;
}
.home-banner-img {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.home-banner-con {
  position: relative;
  z-index: 1;
}
.home-banner .home-banner-con .banner-content {
  width: 50%;
}
.home-banner .home-banner-con .banner-content .banner-cta-div {
  display: flex;
  gap: 0 20px;
  grid-gap: 0 20px;
  margin-top: 50px;
}
.home-banner-img {
  object-position: right;
}

.brain-development {
  position: relative;
  padding: 100px 0 150px;
  min-height: 770px;
}
.brain-development-bk {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.brain-development .brain-content {
  justify-content: flex-end;
}
.brain-development .brain-content .sec-dec {
  margin-top: 35px;
}
.brain-development .brain-content .development-points {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 25px 0;
  margin-top: 30px;
}
.brain-development .brain-content .development-points .development-point {
  display: flex;
  align-items: center;
  gap: 0 15px;
  grid-gap: 0 15px;
}
.brain-development .brain-content .btn-primary-orange {
  margin-top: 40px;
}
.brain-development .brain-bt-bk {
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  object-fit: cover;
}

.elixer-sec {
  position: relative;
  padding: 50px 0;
}
.elixer-sec .sec-title {
  text-align: center;
}
.elixer-sec .sec-dec {
  text-align: center;
  width: 70%;
  margin: 35px auto 0;
}
.elixer-sec .elixer-points {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 15px 15px;
  margin-top: 30px;
}
.elixer-sec .elixer-points .elixer-point {
  padding: 40px 50px;
  text-align: center;
  border: 1px solid #EAF3FA;
  border-radius: 4px;
