}
.elixer-sec .elixer-points .elixer-point:hover .svg-circle {
  fill: var(--orange);
  stroke: var(--white);
}
.elixer-sec .elixer-points .elixer-point:hover .path-main path {
  fill: var(--white);
}
.elixer-sec .elixer-points .elixer-point:hover h5 {
  color: var(--white);
}
.elixer-sec .elixer-sec-bk {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
  z-index: -1;
}

.child-brain {
  padding: 50px 0 100px;
}
.child-brain .sec-title {
  font-family: "Avenir-Medium";
  font-size: 24px;
  line-height: 34px;
  width: 60%;
  margin: 0 auto;
  text-align: center;
}
.child-brain .slide-images-div {
  display: flex;
  align-items: center;
  gap: 15px 15px;
  grid-gap: 15px 15px;
  margin-top: 25px;
}
.child-brain .slide-div {
  position: relative;
  overflow: hidden;
  border-radius: 6px;
  width: 100%;
}
.child-brain .slide-div img {
  width: 100%;
}
.child-brain .slide-div .slide-content {
  position: absolute;
  top: 0;
  left: 0;
  color: #FFF;
  padding: 15px 30px;
  background: rgb(8, 30, 59);
  background: linear-gradient(0deg, rgba(8, 30, 59, 0.66) 0%, rgb(8, 30, 59) 100%);
  height: 100%;
  width: 100%;
  transition: 0.8s;
  transform: translateY(87%);
}
.child-brain .slide-div .slide-content h3 {
  color: var(--white);
  margin-bottom: 20px;
}
.child-brain .slide-div .slide-content ul li {
  font-size: 16px;
  line-height: 26px;
  margin-bottom: 18px;
  background-image: url(../images/home/check-circle-white.svg);
  background-repeat: no-repeat;
  background-size: 14px;
  background-position: left top 6px;
  padding-left: 28px;
}
.child-brain .slide-div:hover .slide-content {
  transition: 0.8s;
  transform: translateY(0%);
}
.child-brain .child-brain-cta {
  margin: 30px 0 0;
  text-align: center;
}

.supplements-sec {
  position: relative;
