  background-color: var(--white);
  color: var(--pc);
}
.faq-sec .accordion button {
  position: relative;
  display: block;
  text-align: left;
  width: 100%;
  padding: 20px;
  color: var(--pc);
  border: 1px solid #DDE0E2;
  border-radius: 4px;
  background-color: transparent;
  outline: none;
}
.faq-sec .accordion button .icon {
  display: inline-block;
  position: absolute;
  top: 22px;
  right: 22px;
  width: 22px;
  height: 22px;
  border: 1px solid;
  border-radius: 22px;
  color: var(--white);
  background-color: var(--pc);
}
.faq-sec .accordion button .icon::before {
  display: block;
  position: absolute;
  content: "";
  top: 9px;
  left: 5px;
  width: 10px;
  height: 2px;
  background: currentColor;
}
.faq-sec .accordion button .icon::after {
  display: block;
  position: absolute;
  content: "";
  top: 5px;
  left: 9px;
  width: 2px;
  height: 10px;
  background: currentColor;
}
.faq-sec .accordion button[aria-expanded=true] {
  color: var(--white);
  background-color: var(--pc);
}
.faq-sec .accordion button[aria-expanded=true] .icon::after {
  width: 0;
}
.faq-sec .accordion button[aria-expanded=true] + .accordion-content {
  opacity: 1;
  max-height: 100%;
  transition: all 200ms linear;
  will-change: opacity, max-height;
  border: 2px solid #F2F2F2;
  box-shadow: 3px 6px 10px #f2f2f2;
}
.faq-sec .accordion .accordion-content {
  opacity: 0;
  max-height: 0;
  overflow: hidden;
  padding: 0 20px;
  background-color: var(--white);
  will-change: opacity, max-height;
}
.faq-sec .accordion .accordion-content div {
  padding: 20px;
}
